import axios from 'axios';
/**
 * @import * from '@/src/typedef';
 */

export default class Url {
  /**
   * @private
   * @param {Api} api
   */
  constructor(api) {
    /**
     * @private
     * @type {Api}
     * */
    this.api = api;
  }

  /**
   * Get url preview data
   * @param {String} url 
   * @returns {Promise<{description: String, images: String, title: String}|Error>}
   */
  getPreview(url) {
    return new Promise(resolve => {
      let apiUrl = `${this.api.config.urlPreviewApi}/`;
      let data = {
        url
      };

      axios
        .get(apiUrl, {
          params: data
        })
        .then(res => {
          resolve({
            description: res.data.Description,
            images: res.data.Images,
            title: res.data.Title
          });
        })
        .catch(() => {
          resolve(null);
        });
    });
  }
}
