import axios from 'axios';
/**
 * @import * from '@/src/typedef';
 */

export default class Auth {
  /**
   * @private
   * @param {Api} api
   */
  constructor(api) {
    /**
     * @private
     * @type {Api}
     */
    this.api = api;
    /**
     * @private
     * @type {Boolean}
     */
    this.isAdmin = false;
  }

  /**
   * Create/Update user info and return token
   * @param {Object} [info]
   * @param {String} [info.id] Client id
   * @param {String} [info.nickname]
   * @param {String} [info.avatarUrl]
   * @param {String} [info.email]
   * @returns {Promise<{token: String}|Error>}
   */
  sign(info = {}) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.authBase}/sign`;
      let data = info;
      let headers = {};

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300) {
              resolve(res.data);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * Verify token and get client id
   * @param {String} token
   * @returns {Promise<{id: String, nickname: String, avatarUrl: String}|Error>}
   */
  verify(token) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.authBase}/verify`;
      let data = {
        token
      };
      let headers = {};

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300) {
              resolve(res.data);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }
}
