import axios from 'axios';
/**
 * @import * from '@/src/typedef';
 */

export default class Message {
  /**
   * @private
   * @param {Api} api
   */
  constructor(api) {
    /**
     * @private
     * @type {Api}
     * */
    this.api = api;
    /**
     * @private
     * @type {Boolean}
     */
    this.isAdmin = false;
  }

  /**
   * Search message
   * @param {String} [limit = 20]
   * @param {Object} [q = {}]
   * @param {String} [q.sender]
   * @param {Object} [q.message]
   * @returns {Promise<{totalCount: Number, data: MessageData[]}|Error>}
   */
  search(limit = 20, q = {}) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/messages`;
      let data = {
        limit,
        q
      };
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .get(url, {
          params: data,
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }
}
