import axios from 'axios';
/**
 * @import * from '@/src/typedef';
 */

export default class Me {
  /**
   * @private
   * @param {Api} api
   */
  constructor(api) {
    /**
     * @private
     * @type {Api}
     * */
    this.api = api;
    /**
     * @private
     * @type {Boolean}
     */
    this.isAdmin = false;
  }

  /**
   * Get my info
   * @returns {Promise<ClientData|Error>}
   */
  getInfo() {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me`;
      let data = {};
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .get(url, {
          params: data,
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * Update my info
   * @param {Object} info
   * @param {String} [info.avatarUrl] 
   * @param {String} [info.email]
   * @param {String} [info.nickname] 
   * @returns {Promise<ClientData|Error>}
   */
  updateInfo(info) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me`;
      let data = info;
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * Get badge
   * @returns {Promise<{badge: Number}|Error>}
   */
  getBadge() {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me/badge`;
      let data = {};
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .get(url, {
          params: data,
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * subscribe
   * @param {String} type ios or ???
   * @param {String} token
   * @returns {Promise<{_id: String, type: String, __v: Number, client: String, token: String, deviceId: String}|Error>}
   */
  subscribe(type, token) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me/subscribe`;
      let data = {
        type,
        token
      };
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * unsubscribe
   * @param {String} type ios or ???
   * @param {String} token
   * @returns {Promise<{n: Number, ok: Number}|Error>}
   */
  unsubscribe(type, token) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me/unsubscribe`;
      let data = {
        type,
        token
      };
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * Mute room
   * @param {String} roomId
   * @returns {Promise<ClientData|Error>}
   */
  muteRoom(roomId) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me/mute/${encodeURIComponent(
        roomId
      )}`;
      let data = {};
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .post(url, data, {
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }

  /**
   * UnMute room
   * @param {String} roomId
   * @returns {Promise<ClientData|Error>}
   */
  unMuteRoom(roomId) {
    return new Promise((resolve, reject) => {
      let url = `${this.api.config.domain}/me/mute/${encodeURIComponent(
        roomId
      )}`;
      let data = {};
      let headers = this.api.getHeader(this.isAdmin);

      axios
        .delete(url, {
          data: data,
          headers: headers
        })
        .then(
          res => {
            if (res.status >= 200 && res.status < 300 && res.data.RC === 0) {
              resolve(res.data.result);
            }
          },
          error => {
            this.api.onError(error);
            reject(error);
          }
        );
    });
  }
}
